<script>
  import { username, user } from './user';
  import { channelId } from './stores.js';
  import { Col, Container, Row } from 'sveltestrap';
  import { Card, CardBody } from 'sveltestrap';
  import { Form, FormGroup, FormText, Input, InputGroup, InputGroupText, Label, Button } from 'sveltestrap';
  import { Icon, Styles } from 'sveltestrap';
  import { ListGroup, ListGroupItem } from 'sveltestrap';

  import { onMount } from 'svelte';
  import { afterUpdate } from 'svelte';

  import GUN from 'gun';
  const db = GUN();

  var curr;
  var account;
  let accounts = [];

  onMount(async () => {
    curr = db.get('addressbook');
    curr.get($username).map().once(function(person){
      accounts = [...accounts.slice(-1*accounts.length), person].sort((a, b) => a.name - b.name);
    });
  });

  afterUpdate(() => {
  });

  function select(channel) {
    console.log(channel)
    channelId.set(channel);
  }

  async function addAccount() {
    curr.get($username).set(
      { id: account, name: account, channel: crox([$username, account]) }
    );
  }

  function crox(array) {
    array.sort();
    var concat = "";
    for (var i=0; i<array.length;i++){
       concat = concat +array[i]
       }
    return concat.toLowerCase()
  }
</script>

  {#if $username}
    <form on:submit|preventDefault={addAccount}>
      <InputGroup>
        <Input  bind:value={account} maxlength="100" />
        <InputGroupText>
        <Button light type="submit" disabled={!account}>
          <Icon name="person-plus-fill" />
        </Button>
        </InputGroupText>
      </InputGroup>
    </form>
    <Row>
      <Card>
        <CardBody>
          <ListGroup flush>
            {#each accounts as { id, name, channel }, i}
              <ListGroupItem tag="a" href="#" on:click={() => select(channel)}>
                {#if name.toLowerCase() == $username.toLowerCase()}
                  Saved messages
                {:else}
                  {name}
                {/if}
              </ListGroupItem>
            {/each}
          </ListGroup>
        </CardBody>
      </Card>
    </Row>
  {:else}
  accounts
  {/if}
